import React, { Component } from 'react';
import { browserHistory } from 'react-router';
import demo from './../styles/demo.css';
import socketio from './../lib/ws_client';
import socketcli from 'socket.io-client';
import tracking from 'tracking';
import ccgui from './../lib/color_camera_gui';
import _ from 'lodash';

class Cam extends Component {
  constructor(props){
    super(props);

    this.state = {
        data: {}
    }
  }

  componentDidMount(){
    let video = document.getElementById('video');
    let canvas = document.getElementById('canvas');
    let context = canvas.getContext('2d');

    let tracker = new tracking.ColorTracker();

    tracking.track('#video', tracker, { camera: true });

    tracker.on('track', function(event) {
      context.clearRect(0, 0, canvas.width, canvas.height);

      event.data.forEach(function(rect) {
        if (rect.color === 'custom') {
          rect.color = tracker.customColor;
        }

        context.strokeStyle = rect.color;
        context.strokeRect(rect.x, rect.y, rect.width, rect.height);
        context.font = '11px Helvetica';
        context.fillStyle = "#fff";
        context.fillText('x: ' + rect.x + 'px', rect.x + rect.width + 5, rect.y + 11);
        context.fillText('y: ' + rect.y + 'px', rect.x + rect.width + 5, rect.y + 22);
      });
    });

   ccgui.initGUIControllers(tracker);
  }

  render() {
    //Define CSS for the camera
    let camdiv = {
      videocanvas: {
        marginLeft: '100px',
        marginTop: '35px',
        position: 'absolute',
      }
    }

    return (
      <div>
        <h1>Hi, move along, the camera will be back</h1>
        <div className="demo-frame">
          <div className="demo-container">
            <video id="video" width="600" height="450" preload autoPlay loop muted controls></video>
            <canvas id="canvas" width="600" height="450"></canvas>
          </div>
        </div>
      </div>

    );
  }
}

export default Cam;



